/* http://prismjs.com/download.html?themes=prism-coy&languages=markup+css+clike+javascript */
/**
 * prism.js Coy theme for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/tshedor/workshop-wp-theme (Example: http://workshop.kansan.com/category/sessions/basics or http://workshop.timshedor.com/category/sessions/basics);
 * @author Tim  Shedor
 */
code[class*="language-"],
pre[class*="language-"] {
  color: black;
  background: none;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}
/* Code blocks */
pre[class*="language-"] {
  position: relative;
  margin: .5em 0;
  box-shadow: -1px 0px 0px 0px #358ccb, 0px 0px 0px 1px #dfdfdf;
  border-left: 10px solid #358ccb;
  background-color: #fdfdfd;
  background-image: linear-gradient(transparent 50%, rgba(69, 142, 209, 0.04) 50%);
  background-size: 3em 3em;
  background-origin: content-box;
  overflow: visible;
  padding: 0;
}
code[class*="language"] {
  max-height: inherit;
  height: 100%;
  padding: 0 1em;
  display: block;
  overflow: auto;
}
/* Margin bottom to accomodate shadow */
:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background-color: #fdfdfd;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin-bottom: 1em;
}
/* Inline code */
:not(pre) > code[class*="language-"] {
  position: relative;
  padding: .2em;
  border-radius: 0.3em;
  color: #c92c2c;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: inline;
  white-space: normal;
}
pre[class*="language-"]:before,
pre[class*="language-"]:after {
  content: '';
  z-index: -2;
  display: block;
  position: absolute;
  bottom: 0.75em;
  left: 0.18em;
  width: 40%;
  height: 20%;
  max-height: 13em;
  box-shadow: 0px 13px 8px #979797;
  -webkit-transform: rotate(-2deg);
  -moz-transform: rotate(-2deg);
  -ms-transform: rotate(-2deg);
  -o-transform: rotate(-2deg);
  transform: rotate(-2deg);
}
:not(pre) > code[class*="language-"]:after,
pre[class*="language-"]:after {
  right: 0.75em;
  left: auto;
  -webkit-transform: rotate(2deg);
  -moz-transform: rotate(2deg);
  -ms-transform: rotate(2deg);
  -o-transform: rotate(2deg);
  transform: rotate(2deg);
}
.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #7D8B99;
}
.token.punctuation {
  color: #5F6364;
}
.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.function-name,
.token.constant,
.token.symbol,
.token.deleted {
  color: #c92c2c;
}
.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.function,
.token.builtin,
.token.inserted {
  color: #2f9c0a;
}
.token.operator,
.token.entity,
.token.url,
.token.variable {
  color: #a67f59;
  background: rgba(255, 255, 255, 0.5);
}
.token.atrule,
.token.attr-value,
.token.keyword,
.token.class-name {
  color: #1990b8;
}
.token.regex,
.token.important {
  color: #e90;
}
.language-css .token.string,
.style .token.string {
  color: #a67f59;
  background: rgba(255, 255, 255, 0.5);
}
.token.important {
  font-weight: normal;
}
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}
.token.entity {
  cursor: help;
}
.namespace {
  opacity: .7;
}
@media screen and (max-width: 767px) {
pre[class*="language-"]:before,
  pre[class*="language-"]:after {
    bottom: 14px;
    box-shadow: none;
}
}
/* Plugin styles */
.token.tab:not(:empty):before,
.token.cr:before,
.token.lf:before {
  color: #e0d7d1;
}
/* Plugin styles: Line Numbers */
pre[class*="language-"].line-numbers {
  padding-left: 0;
}
pre[class*="language-"].line-numbers code {
  padding-left: 3.8em;
}
pre[class*="language-"].line-numbers .line-numbers-rows {
  left: 0;
}
/* Plugin styles: Line Highlight */
pre[class*="language-"][data-line] {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0;
}
pre[data-line] code {
  position: relative;
  padding-left: 4em;
}
pre .line-highlight {
  margin-top: 0;
}
@font-face {
  font-family: 'Roboto Mono';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto Mono'), local('RobotoMono-Regular'), url(assets/demo/fonts/robotomono.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215;
}
h1 {
  font-size: 2.6em;
}
h2 {
  font-size: 2.15em;
}
h3 {
  font-size: 1.7em;
}
h4 {
  font-size: 1.25em;
}
h5 {
  font-size: 1em;
}
h6 {
  font-size: 0.85em;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1.5em 0;
  text-align: start;
}
pre {
  word-break: break-word;
  font-family: 'Roboto Mono', Monaco, courier, monospace;
}
p,
pre,
pre.prettyprint,
blockquote {
  margin: 0 0 1.1em;
}
hr {
  margin: 2em 0;
}
img {
  max-width: 100%;
}
code,
pre {
  font-size: 13px;
}
code {
  white-space: normal;
}
pre,
pre.prettyprint {
  text-align: start;
  border: 0;
  padding: 20px;
  border-radius: 5px;
  background: #f9f9f9;
  line-height: 18px;
}
pre code,
pre.prettyprint code {
  background-color: transparent !important;
}
dt,
dd {
  margin-top: 5px;
  margin-bottom: 5px;
}
dd {
  margin-left: 40px;
}
.markdown-table {
  margin-bottom: 20px;
}
.markdown-table th,
.markdown-table td {
  padding: 8px;
  line-height: 20px;
  vertical-align: top;
  border-top: 1px solid #ddd;
}
.markdown-table th {
  font-weight: bold;
}
.markdown-table thead th {
  vertical-align: bottom;
}
.markdown-table caption + thead tr:first-child th,
.markdown-table caption + thead tr:first-child td,
.markdown-table colgroup + thead tr:first-child th,
.markdown-table colgroup + thead tr:first-child td,
.markdown-table thead:first-child tr:first-child th,
.markdown-table thead:first-child tr:first-child td {
  border-top: 0;
}
.markdown-table tbody + tbody {
  border-top: 2px solid #ddd;
}
blockquote {
  padding: 10.5px 21px;
  margin: 0 0 21px;
  border-left: 5px solid rgba(128, 128, 128, 0.075);
}
blockquote {
  border-left-width: 10px;
  background-color: rgba(128, 128, 128, 0.05);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 15px 20px;
}
blockquote p {
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 1em;
  line-height: 2;
}
blockquote ul:last-child,
blockquote ol:last-child {
  margin-bottom: 0;
}
ul,
ol {
  margin-bottom: 1.1em;
}
ul ul,
ol ul,
ul ol,
ol ol {
  margin-bottom: 1.1em;
}
a {
  color: #4fa1db;
  text-decoration: none;
  -webkit-transition: background-color ease-in-out 0.15s, color ease-in-out 0.15s, border-color ease-in-out 0.15s;
  transition: background-color ease-in-out 0.15s, color ease-in-out 0.15s, border-color ease-in-out 0.15s;
}
a:hover,
a:focus {
  color: #216da3;
  text-decoration: underline;
}
a:focus {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
body {
  font-weight: 400;
}
img {
  vertical-align: middle;
}
header {
  height: 80px;
  background: #fff;
  border-bottom: 1px solid #f1f1f1;
}
header .logo {
  font-size: 22px;
  line-height: 75px;
  height: 80px;
  color: #fff;
  display: block;
  text-align: center;
}
header .logo:hover {
  text-decoration: none;
}
header .logo img {
  width: 32px;
  height: 32px;
}
header .logo-text {
  position: absolute;
  color: #46c3c1;
  margin-left: 15px;
}
footer {
  clear: both;
  font-size: 14px;
  background: #fff;
  position: relative;
  z-index: 1;
  color: rgba(0, 0, 0, 0.65);
  box-shadow: 0 1000px 0 1000px #fff;
  height: 120px;
}
footer .footer-main {
  text-align: center;
  padding-top: 50px;
}
footer .footer-main .footer-social {
  margin-top: 15px;
}
footer .footer-main .footer-social .x-icon {
  font-size: 36px;
}
#xcui-site-container {
  background: #eee;
}
#wrapper {
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  margin: 30px auto 20px;
  width: 90%;
}
#wrapper-content {
  background: #fff;
  border-radius: 4px;
  padding-bottom: 30px;
}
#sidebar-wrapper {
  padding-top: 30px;
  z-index: 1000;
  display: inline-block;
  height: 100%;
  overflow-x: hidden;
  background: #fff;
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
}
#wrapper.toggled #sidebar-wrapper {
  width: 250px;
}
#page-content-wrapper {
  background: #fff;
}
.x-demo {
  color: rgba(0, 0, 0, 0.65);
}
.x-demo .x-demo-inner {
  padding-right: 40px;
}
.x-demo h1,
.x-demo h2,
.x-demo h3,
.x-demo h4,
.x-demo h5,
.x-demo h6 {
  font-weight: 400;
}
.x-demo h1 {
  font-size: 26px;
}
.x-demo h2 {
  font-size: 22px;
}
.x-demo h3 {
  font-size: 16px;
}
.x-demo h4 {
  font-size: 14px;
}
.x-demo h5,
.x-demo h6 {
  font-size: 12px;
}
.x-demo code {
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f7f7f7;
  border: 1px solid #eee;
  border-radius: 4px;
  margin: 0 5px;
}
.x-demo .markdown-table {
  font-family: 'Roboto Mono', Monaco, Courier, monospace;
  font-size: 13px;
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #e9e9e9;
  width: 100%;
  margin-bottom: 24px;
  color: rgba(0, 0, 0, 0.65);
}
.x-demo .markdown-table th {
  background: #f7f7f7;
  white-space: nowrap;
  color: #5c6b77;
  font-weight: 600;
}
.x-demo .markdown-table td,
.x-demo .markdown-table th {
  border: 1px solid #e9e9e9;
  padding: 8px 16px;
  text-align: left;
}
.x-demo ul li {
  margin-bottom: 1em;
}
/* Sidebar Styles */
.sidebar-nav {
  margin: 0;
  padding-left: 10px;
  list-style: none;
  overflow-x: hidden!important;
  border-right: 1px solid #eee;
}
.sidebar-nav .router-link-active {
  color: #46c3c1;
  border-right: 3px solid #46c3c1;
}
.sidebar-nav li {
  text-indent: 20px;
  line-height: 40px;
}
.sidebar-nav li.sidebar-divider {
  border-bottom: 1px solid #eee;
  margin-top: 10px;
  margin-right: 15px;
}
.sidebar-nav li.sidebar-part-title {
  font-size: 16px;
  margin-top: 10px;
}
.sidebar-nav li.sidebar-title {
  color: #b2b2b2;
}
.sidebar-nav li.sidebar-item:hover {
  background: rgba(190, 239, 239, 0.15);
  cursor: pointer;
}
.sidebar-nav li.sidebar-item a {
  padding-left: 20px;
}
.sidebar-nav li a {
  display: block;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.65);
  border-left: 2px solid #fff;
}
.sidebar-nav li a.active,
.sidebar-nav li a.focus {
  text-decoration: none;
}
.sidebar-nav li a.v-link-active {
  border-left: 2px solid #46c3c1;
  background: #fff;
  color: #46c3c1;
}
.sidebar-nav .sidebar-brand {
  height: 65px;
  font-size: 18px;
  line-height: 60px;
}
.sidebar-nav .sidebar-brand a {
  color: #46c3c1;
  background: #fff;
}
.sidebar-nav .sidebar-brand a :hover {
  color: #fff;
  background: #46c3c1;
}



