.base-table {
  font-size: 12px;
  margin: -1px;
  border-collapse: collapse;
}
.base-table td {
  text-align: center;
  padding: 20px 3px;
  cursor: pointer;
}
.base-table td .cell {
  width: 48px;
  height: 32px;
  display: block;
  line-height: 32px;
  color: #555555;
  text-decoration: none;
}
.base-table td .cell:hover {
  background-color: rgba(70, 195, 193, 0.15);
}
.base-table td.current:not(.disabled) .cell {
  background-color: #46c3c1 !important;
  color: #fff;
}
.base-table td.disabled .cell {
  background-color: #f7f7f7;
  cursor: not-allowed;
  color: #ccc;
}
