  background-color: #2c96ef;
}
.x-steps-item-default.active .x-steps-item-title-text {
  color: #2c96ef;
}
.x-steps-item-running .x-steps-item-title-mark-circle {
  width: 50%;
  overflow: hidden;
  transform-origin: 100% 50%;
  animation: steps-running-rotate 0.8s linear infinite;
}
@keyframes steps-running-rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.x-steps-item-running .x-steps-item-title-mark-circle::after {
  width: 200%;
  border-width: 3px;
}
.x-steps-item-running.active .x-steps-item-title-mark-text {
  color: #806000;
}
.x-steps-item-running.active .x-steps-item-title-mark-circle::after {
  background-color: transparent;
  border-width: 5px;
}
.x-steps-overflow {
  position: absolute;
  z-index: 2;
  width: 80px;
  top: 0;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.7), #ffffff);
  opacity: 0;
  transition: opacity 0.5s;
}
.x-steps-overflow-visible {
  opacity: 1;
}
.x-steps-overflow-right {
  right: 0;
}
.x-steps-overflow-left {
  left: 0;
  transform: rotate(180deg);
}
.x-collapse {
  border: 1px solid #d9d9d9;
  border-radius: 2px;
}
.x-collapse-item-header {
  padding: 15px;
  border-bottom: 1px solid #d9d9d9;
  background: #f9f9f9;
  cursor: pointer;
}
.x-collapse-item-wrap {
  transition: height 0.3s ease-in-out;
  overflow: hidden;
  cursor: default;
}
.x-collapse-item-content {
  border-bottom: 1px solid #d9d9d9;
  padding: 15px;
}
.x-collapse-item:last-child .x-collapse-item-header {
  border-bottom: none;
}
.x-collapse-item:last-child .x-collapse-item-content {
  border-bottom: none;
  border-top: 1px solid #d9d9d9;
}



