
.demo-tooltip .box {
  width: 400px;
}
.demo-tooltip .top {
  text-align: center;
}
.demo-tooltip .left {
  float: left;
  width: 60px;
}
.demo-tooltip .right {
  float: right;
  width: 60px;
}
.demo-tooltip .bottom {
  text-align: center;
  clear: both;
}
.demo-tooltip .btn {
  margin: 6px;
}
.my-fade-enter-active,
.my-fade-leave-active {
  transition: opacity 0.5s;
}
.my-fade-enter,
.my-fade-leave-to {
  opacity: 0;
}



