
.xcui-demo-container {
  border: 1px solid #eee;
  border-radius: 4px;
  margin-bottom: 15px;
}
.xcui-demo-container .xcui-example-container {
  padding: 15px;
}
.xcui-demo-container .xcui-example-container h2 {
  margin: 0 0 15px 0;
}
.xcui-demo-container .xcui-example-container .xcui-info-wrap .xcui-info-header {
  padding: 15px 0;
}
.xcui-demo-container .xcui-example-container .xcui-info-wrap .xcui-info-header:before {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #eee;
  position: relative;
  top: 10px;
  left: 0;
}
.xcui-demo-container .xcui-example-container .xcui-info-wrap .xcui-info-header span {
  display: inline-block;
  background: #fff;
  padding: 0 18px 0 18px;
  position: relative;
  margin-left: 30px;
  font-size: 14px;
  font-weight: 500;
}
.xcui-demo-container .xcui-example-container .xcui-info-wrap .xcui-info-content {
  padding: 15px 0 25px 0;
}
.xcui-demo-container .xcui-example-container .xcui-info-wrap .xcui-info-content p {
  margin: 0;
  line-height: 28px;
}
.xcui-demo-container .xcui-example-container .expand-wrap {
  overflow: hidden;
  height: 0;
  transition: height 0.5s;
}
.xcui-demo-container .xcui-example-container .expand-wrap .xcui-code-wrap {
  max-width: 100%;
  margin-top: 5px;
  box-sizing: content-box;
}
.xcui-demo-container .xcui-example-container .expand-wrap .xcui-code-wrap pre {
  padding: 0;
  padding-left: 20px;
  display: block;
  font-size: 13px;
  line-height: 1.42857143;
  color: #333;
  word-break: break-all;
  word-wrap: break-word;
  background-color: rgba(128, 128, 128, 0.05);
  border: 1px solid rgba(128, 128, 128, 0.075);
  border-radius: 4px;
  white-space: pre-wrap;
}
.xcui-demo-container .expand-control {
  text-align: center;
  padding: 10px 0;
  border-top: 1px solid #eee;
  cursor: pointer;
}
.xcui-demo-container .expand-control i {
  font-size: 14px;
  margin-right: 5px;
}



