
.demo-popover .box {
  width: 400px;
}
.demo-popover .top {
  text-align: center;
}
.demo-popover .left {
  float: left;
  width: 60px;
}
.demo-popover .right {
  float: right;
  width: 60px;
}
.demo-popover .bottom {
  text-align: center;
  clear: both;
}
.demo-popover .btn {
  margin: 6px;
}
.my-fade-enter-active,
.my-fade-leave-active {
  transition: opacity 0.5s;
}
.my-fade-enter,
.my-fade-leave-to {
  opacity: 0;
}



